import React, { useEffect } from 'react';
import { Link } from 'react-router-dom';
import './navbar.scss';

const Navbar = () => {
  useEffect (() => {
    activeLink();
    addEvent();

    return () => {
      activeLink();
      addEvent();
    }
  })

    let debounce = (func, wait = 10, immediate = true) => {
      let timeout;

      return function () {
        let context = this,
          args = arguments;
        let later = function () {
          timeout = null;

          if (!immediate) func.apply(context, args);
        };

        let callNow = immediate && !timeout;
        clearTimeout(timeout);
        timeout = setTimeout(later, wait);
        if (callNow) func.apply(context, args);
      };
    };

    let changePosition = () => {
      let scrollTop = window.pageYOffset || document.documentElement.scrollTop;
      let navbar = document.getElementById("navbar");

      if (scrollTop >= 100 && scrollTop < 200 && navbar.classList.contains("fadeInDown")) {
        navbar.classList.remove("fadeInDown");
      }

      if (scrollTop > 500 && !navbar.classList.contains("fixed")) {
        navbar.classList.add("fadeInDown");
        navbar.classList.add("fixed");
      }
      
      if (scrollTop < 500 && navbar.classList.contains("fixed")) {
        navbar.classList.add("fadeOutUp");

        setTimeout(() => {
          navbar.classList.remove("fixed", "fadeOutUp");
          navbar.classList.add("fadeInDown");
        }, 1000);
      }
    }

    let addEvent = () => {
      let screenWidth = window.innerWidth;
      if (screenWidth > 990) {
        window.addEventListener("scroll", debounce(changePosition));
      }
    };

  let activeLink = () => {
    const link = window.location.pathname
    const navigationItem = Array.from(
      document.querySelectorAll(".navbar .nav-link a")
    );

    navigationItem.forEach((item) => {link === item.getAttribute("href")
      ? item.classList.add("is-active")
      : item.classList.remove("is-active");})
  }
  

  let toggleMenu = () => {
        const dropdown = document.querySelector('.navbar-collapse');
        const burger = document.querySelector('.navbar-burger');

        
        dropdown.style.display = "block"; 
        burger.classList.toggle('is-active');

        if (burger.classList.contains("is-active")) {
          setTimeout(() => {
            dropdown.classList.add("is-active");
          }, 0);
        } else {
          dropdown.classList.remove("is-active");
          setTimeout(() => {
            dropdown.style.display = "none";
          }, 300);
        }
    }

      return (
        <nav id="navbar" className="navbar fadeInDown">
          <div className="container">
            <div className="navbar-mobile">
              <p className="navbar-brand">UZBEGIM</p>
              <button
                className="navbar-toggler"
                type="button"
                onClick={toggleMenu}
              >
                <div className="navbar-burger">
                  <div className="bar1"></div>
                  <div className="bar2"></div>
                  <div className="bar3"></div>
                </div>
                <span>Menu</span>
              </button>
            </div>
              <div className="navbar-collapse">
                <ul className="navbar-nav">
                  <li className="nav-item">
                    <p className="nav-link">
                      <Link to="/uzbegim-app">Home</Link>
                    </p>
                  </li>
                  <li className="nav-item">
                    <p className="nav-link">
                      <Link to="/uzbegim-app/menu">Menu</Link>
                    </p>
                  </li>
                  <li className="nav-item">
                    <p className="nav-link">
                      <Link to="/uzbegim-app/contact">Contact</Link>
                    </p>
                  </li>
                </ul>
              </div>
          </div>
        </nav>
      );
};

export default Navbar