import React, { useState } from "react";

import Tiles from "../../components/tiles/tiles";
import Tabs from "../../components/tabs/tabs";
import foodImage from "../../assets/images/homepage/dessert-5.jpg";

let MenuListMarkup = () => {
  let menu = [
    {
      id: "soups",
      submenu: "Polevky",
      tiles: [
        {
          id: 1,
          title: "Polevka 1",
          image: foodImage,
          description:
            "some text which describes the food and how delicios it is",
          button: true,
          price: "150 KC",
        },
        {
          id: 2,
          title: "Polevka 2",
          image: foodImage,
          description:
            "some text which describes the food and how delicios it is",
          button: true,
          price: "150 KC",
        },
        {
          id: 3,
          title: "Polevka 3",
          image: foodImage,
          description:
            "some text which describes the food and how delicios it is",
          button: true,
          price: "150 KC",
        },
        {
          id: 4,
          title: "Polevka 4",
          image: foodImage,
          description:
            "some text which describes the food and how delicios it is",
          button: true,
          price: "150 KC",
        },
      ],
    },

    {
      id: "main",
      submenu: "Hlavni Jidla",
      tiles: [
        {
          id: 5,
          title: "Hlavni Jidlo 1",
          image: foodImage,
          description:
            "some text which describes the food and how delicios it is",
          button: true,
          price: "150 KC",
        },
        {
          id: 6,
          title: "Hlavni Jidlo 2",
          image: foodImage,
          description:
            "some text which describes the food and how delicios it is",
          button: true,
          price: "150 KC",
        },
        {
          id: 7,
          title: "Hlavni Jidlo 3",
          image: foodImage,
          description:
            "some text which describes the food and how delicios it is",
          button: true,
          price: "150 KC",
        },
        {
          id: 8,
          title: "Hlavni Jidlo 4",
          image: foodImage,
          description:
            "some text which describes the food and how delicios it is",
          button: true,
          price: "150 KC",
        },
      ],
    },

    {
      id: "other",
      submenu: "Other",
      tiles: [
        {
          id: 9,
          title: "Other 1",
          image: foodImage,
          description:
            "some text which describes the food and how delicios it is",
          button: true,
          price: "150 KC",
        },
        {
          id: 10,
          title: "Other 2",
          image: foodImage,
          description:
            "some text which describes the food and how delicios it is",
          button: true,
          price: "150 KC",
        },
        {
          id: 11,
          title: "Other 3",
          image: foodImage,
          description:
            "some text which describes the food and how delicios it is",
          button: true,
          price: "150 KC",
        },
        {
          id: 12,
          title: "Other 4",
          image: foodImage,
          description:
            "some text which describes the food and how delicios it is",
          button: true,
          price: "150 KC",
        },
      ],
    },
  ];

  const [chosen, setChosen] = useState(menu);

  let onTabchange = (e) => {
    let arr = [];
    let t = e.target.id;
    let buttons = Array.from(document.querySelectorAll(".tabs-wrapper button"));
    let selected = menu.find((element) => `${element.id}-button` === t);

    buttons.forEach((element) =>
      element.id === t
        ? element.classList.add("is-active")
        : element.classList.remove("is-active")
    );
    arr.push(selected);

    setChosen(arr);
  };

  let tabRender = menu.map((item, index) => (
    <Tabs
      onTabchange={onTabchange}
      key={index.toString()}
      buttonName={item.submenu}
      buttonId={item.id}
    />
  ));

  let menuMarkup = chosen.map((item, index) => {
    let { tiles, id } = item;

    return (
        <div key={index.toString()} id={id}>
          <Tiles tiles={tiles} theme="theme-2" />
        </div>
    );
  });

  return (
    <div>
      <div className="tabs-wrapper">{tabRender}</div>
      {menuMarkup}
    </div>
  );
};


const MenuList = () => {
  return <MenuListMarkup />;
}

export default MenuList;